//表单操作枚举
const formAction = {
    //新增操作
    ADD: "ADD",

    //编辑操作
    EDIT: "EDIT"    
}

//登录模式枚举
const loginMode = {
    //登录前台
    FRONTEND: "FRONTEND",

    //登录后台
    BACKEND: "BACKEND"
}

//设置参数
const settings = {
    //通用参数
    common: {
        pageSize: 15,
        copyright: "版权所有&copy; 浙江捷尚视频科技股份有限公司"
    },
    //前台参数
    frontend: {
        //车型搜索结果列表分页设置
        pageSize: 30,
        //前端登录后默认页面
        homePage: '/home',
        //显示无结果字符
        noResultText: "检索到0个结果"
    },
    //后端参数
    backend:{
        pageSize: 30,
        //后端登录后默认页面
        homePage: '/car/list'
    }
}

export default{
    formAction,
    loginMode,
    settings
}


