import userface from '@/assets/images/userface.png'

export default {
      initMenu(state, menus){
        state.routes = menus;
      },
      login(state, user){      
        state.user = user;
        state.user.userface = userface;
        window.localStorage.setItem('user', JSON.stringify(user));
        state.routes = null;      
      },      
      logout(state){
        window.localStorage.removeItem('user');
        state.user = {};
        state.routes = null;
      } 
}


