import axios from 'axios'
import {Message} from 'element-ui'
import store from '../store'

axios.interceptors.request.use(config=> {
  //认证以后，需要在请求头中设置token
  if (store.state.user && store.state.user.token) {   
    config.headers.Authorization = 'Bearer ' + store.state.user.token
    console.log("token:" + store.state.user.token);
  }

  return config;
}, err=> {
  Message.error({message: '请求超时!'});
  return Promise.resolve(err);
})

let toLogin = function(){
  setTimeout(() => {
    store.commit('logout');
    window.location.href= "/";  
  }, 2000);
}

axios.interceptors.response.use(data=> {
  if (!data.data.success) {
    Message.error({message: data.data.error});
  }
  return data.data;
}, err=> { 
  if(err.response == undefined){
    Message.error({message: '访问服务器失败!'});
  }else{
    if (err.response.status == 401 || err.response.status == 403) {    
      Message.error({message: '您当前还未认证或认证已过期，即将为您跳转到登录界面!'});
      toLogin();
    }else if (err.response.status == 404||err.response.status == 504) {
      Message.error({message: '请求的资源不存在!'});
    }else {
      console.error("未知错误[" + err.response.status + "]")
      Message.error({message: '未知错误!'});
    }    
  }
  
  return Promise.resolve(err.response.data);
})

let base = '/api';

export const postRequest = (url, params) => {
  return axios({
    method: 'post',
    url: `${base}${url}`,
    data: params,
    transformRequest: [function (data) {
      let ret = ''
      for (let it in data) {
        ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
      }
      return ret
    }],
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  });
}

export const uploadFileRequest = (url, params) => {
  return axios({
    method: 'post',
    url: `${base}${url}`,
    data: params,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  });
}

export const putRequest = (url, params) => {
  return axios({
    method: 'put',
    url: `${base}${url}`,
    data: params,
    transformRequest: [function (data) {
      let ret = ''
      for (let it in data) {
        ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
      }
      return ret
    }],
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  });
}

export const deleteRequest = (url) => {
  return axios({
    method: 'delete',
    url: `${base}${url}`
  });
}

export const getRequest = (url) => {  
  console.info("getRequest:" + url)
  return axios({
    method: 'get',
    url: `${base}${url}`
  });
}


