<template>
  <div class="container">
    <div class="header">

      <el-row class="header-row">
        <el-col :span="10" class="logo"><img src="../assets/images/frontend/logo.png" width="122" height="30" /></el-col>
        <el-col :span="4" class="userinfo">
          <top-user parent="home" :enable-home-menu="false" :enable-manage-menu="true"></top-user>
        </el-col>
      </el-row>

    </div>
    <div class="main">

      <el-row class="main-row">
        <el-row class="main-row-filter" v-show="filterVisible">
          <el-col class="upload-container">
            <image-selector ref="imageSelector" />
          </el-col>
          <el-col class="filter-container">
            <lazy-render :time="300">
                <filter-condition @changed="onFilterChanged" />
            </lazy-render>                        
          </el-col>
        </el-row>
        <el-row class="main-row-filter-collapse-bar">
          <button @click="onCollapseClick" :class="filterVisible ? '': 'actived'" />
        </el-row>
        <el-row class="main-row-filter-tag">
          <el-col :span="20">
            <filter-tag-bar ref="filterTagBar" />
          </el-col>
          <el-col :span="4" class="filter-result-count">
            <span>搜索结果：共 {{totalCount}} 条</span>
          </el-col>
        </el-row>
        <el-row class="main-row-separate-line">
        </el-row>
        <el-row class="main-row-filter-result" v-loading="loading">
          <filter-result ref="filterResult" :source="items" v-show="hasResult" v-on:contrastShow="contrastShow" />
          <div class="no-result" v-show="showNoResult">{{noResultText}}</div>
        </el-row>
        <el-row class="main-row-result-pagination">
          <el-pagination background :page-sizes="[pageSize, 50, 100, 200, 300]" :page-size="pageSize" :current-page="pageIndex" :total="totalCount" @current-change="doTurnPage" layout="sizes, prev, pager, next, jumper"></el-pagination>
        </el-row>
      </el-row>

    </div>
    <div class="footer">
      <copyright></copyright>
    </div>

    <vehicle-contrast :picId="picId" :originalUrl="originalUrl" ref="vehicleContrast"></vehicle-contrast>    
  </div>
</template>
<script>
import { searchVehicles } from "@/api";
import topUser from "./common/TopUser.vue";
import filterCondition from "./home/FilterCondition.vue";
import filterTagBar from "./home/FilterTagBar.vue";
import filterResult from "./home/FilterResult.vue";
import imageSelector from "./home/ImageSelector.vue";
import vehicleContrast from "@/components/common/VehicleContrast.vue";
import copyright from "@/components/common/Copyright.vue"
import lazyRender from "@/components/base/LazyRender.vue"

export default {
  components: {
    topUser,
    filterCondition,
    filterTagBar,
    filterResult,
    imageSelector,
    vehicleContrast,
    copyright,
    lazyRender
  },
  data() {
    return {
      params: {},
      pageSize: this.GLOBAL.settings.frontend.pageSize,
      pageIndex: 1,
      items: [],
      totalCount: 0,
      loading: true,
      noResultText: this.GLOBAL.settings.frontend.noResultText,
      selectedImage: null,
      filterVisible: true,

      picId: "",
      originalUrl: ""
    };
  },
  methods: {
    doFilter: function() {
      const self = this;

      self.loading = true;

      searchVehicles({
        paramMap: JSON.stringify(self.params).replace(/\"/g,"'"),
        pageIndex: self.pageIndex,
        pageSize: self.pageSize
      }).then(res => {
        self.loading = false;
        if (res.success) {
          self.totalCount = res.data.totalCount;
          self.items = res.data.items;
        }
      });
    },
    onFilterChanged: function(params) {
      this.params = params;
      this.doFilter();
    },
    doTurnPage: function(currentChange) {
      this.pageIndex = currentChange;
      this.doFilter();
    },
    onCollapseClick: function() {
      this.filterVisible = !this.filterVisible;
    },
    contrastShow: function(itemId) {
      this.picId = itemId;
      if ((this.$refs.imageSelector.picZoomEnable == true)) {
        this.originalUrl = this.$refs.imageSelector.imageSource;
      } else {
        this.originalUrl = "";
      }

      this.$refs.vehicleContrast.show();
    }    
  },
  mounted() {
    this.doFilter();
  },
  computed: {
    user() {
      return this.$store.state.user;
    },
    hasResult() {
      return this.items.length > 0;
    },
    showPagination() {
      return this.totalCount > this.pageSize;
    },
    showPlus() {
      return this.selectedImage == null;
    },
    showImage() {
      return this.selectedImage != null;
    },
    showNoResult(){
      return !this.hasResult && !this.loading;
    }
  }
};
</script>

<style lang="scss">
@import "../assets/styles/frontend-common.scss";
</style>
<style lang="scss" scoped>
@import "../assets/styles/vars.scss";
@import "./Home.scss";
</style>





