<template>
  <el-dialog :title="title" :visible.sync="visible" :width="width" :close-on-click-modal="false" :before-close="onClose" append-to-body>
    <div id="contrast-box">
      <div class="pic-box">
        <pic-zoom :url="originalUrl" :scale="2.5" :onerror="defaultImg"></pic-zoom>
      </div>
      <div class="pic-box">
        <pic-zoom :url="contrastUrl" :scale="2.5" ref="constrastBox" :onerror="defaultImg"></pic-zoom>
      </div>
    </div>
    <div id="contrast-table">
      <el-table :data="contrast" header-align="center" border size="mini" :highlight-current-row="false">
        <el-table-column prop="vehicleType" label="车型比对" width="148" :resizable="false" header-align="center" align="center">
        </el-table-column>
        <el-table-column :label="labelFirst" width="110" :resizable="false" header-align="center" align="center">
          <template slot-scope="scope">
            <img class="img-small" :src="scope.row.srcFirst" @click="cellClick(0)" :onerror="defaultImg">
          </template>
        </el-table-column>
        <el-table-column :label="labelSecond" width="110" :resizable="false" header-align="center" align="center">
          <template slot-scope="scope">
            <img class="img-small" :src="scope.row.srcSecond" @click="cellClick(1)" :onerror="defaultImg">
          </template>
        </el-table-column>
        <el-table-column :label="labelThird" width="110" :resizable="false" header-align="center" align="center">
          <template slot-scope="scope">
            <img class="img-small" :src="scope.row.srcThird" @click="cellClick(2)" :onerror="defaultImg">
          </template>
        </el-table-column>
        <el-table-column :label="labelFourth" width="110" :resizable="false" header-align="center" align="center">
          <template slot-scope="scope">
            <img class="img-small" :src="scope.row.srcFourth" @click="cellClick(3)" :onerror="defaultImg">
          </template>
        </el-table-column>
        <el-table-column :label="labelFifth" width="110" :resizable="false" header-align="center" align="center">
          <template slot-scope="scope">
            <img class="img-small" :src="scope.row.srcFifth" @click="cellClick(4)" :onerror="defaultImg">
          </template>
        </el-table-column>
      </el-table>
    </div>
  </el-dialog>
</template>

<script>
import PicZoom from "@/components/base/PicZoom";
import defaultcar from "@/assets/images/defaultcar.jpg";
import { getCarInfo } from "@/api";
import { getVehicleDescriptionById } from "@/api";
export default {
  name: "VehicleContrast",
  components: {
    PicZoom
  },
  props: ["picId", "originalUrl"],
  data() {
    return {
      title: "车型对比",
      visible: false,
      width: "740px",
      //如果图片获取失败显示图片
      defaultImg: 'this.src="' + require("../../assets/images/default.png") + '"',

      //idtemp:"74970c9c-0733-4287-9506-f50e7af7a720",
      //originalUrl: defaultcar,
      pictureData: [],
      contrastUrl: defaultcar,
      contrast: [
        {
          vehicleType: "",
          srcFirst: defaultcar,
          srcSecond: defaultcar,
          srcThird: defaultcar,
          srcFourth: defaultcar,
          srcFifth: defaultcar
        }
      ],
      labelFirst: "",
      labelSecond: "",
      labelThird: "",
      labelFourth: "",
      labelFifth: ""
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.initBox();
    });
  },
  methods: {
    initBox() {
      if (!this.picId) return;
      getCarInfo(this.picId).then(res => {
        if (res.success) {
          let data = res.data.pictureOutputs;
          this.pictureData = data;
          if (data&&data[0]) {
            this.contrastUrl = data[0].orginnalname;
            this.contrast[0].srcFirst = data[0].smallname;
            this.labelFirst = data[0].name;
          } else {
            this.contrastUrl = defaultcar;
            this.contrast[0].srcFirst = defaultcar;
            this.labelFirst = "";
          }
          if (data&&data[1]) {
            this.contrast[0].srcSecond = data[1].smallname;
            this.labelSecond = data[1].name;
          } else {
            this.contrast[0].srcSecond = defaultcar;
            this.labelSecond = "";
          }
          if (data&&data[2]) {
            this.contrast[0].srcThird = data[2].smallname;
            this.labelThird = data[2].name;
          } else {
            this.contrast[0].srcThird = defaultcar;
            this.labelThird = "";
          }
          if (data&&data[3]) {
            this.contrast[0].srcFourth = data[3].smallname;
            this.labelFourth = data[3].name;
          } else {
            this.contrast[0].srcFourth = defaultcar;
            this.labelFourth = "";
          }
          if (data&&data[4]) {
            this.contrast[0].srcFifth = data[4].smallname;
            this.labelFifth = data[4].name;
          } else {
            this.contrast[0].srcFifth = defaultcar;
            this.labelFifth = "";
          }
        }
      });
      getVehicleDescriptionById(this.picId).then(res => {
        if (res.success) {
          this.contrast[0].vehicleType = res.data.description;
        }
      });
    },
    show() {
      this.visible = true;
    },
    onClose() {
      this.visible = false;
    },
    cellClick(picUrl) {
      if (this.pictureData[picUrl]) {
        this.contrastUrl = this.pictureData[picUrl].orginnalname;
      }
    }
  },
  watch: {
    picId(val) {
      if (val != undefined) {
        this.initBox();
      }
    }
  }
};
</script>

<style>
#contrast-box {
  width: 700px;
  height: 270px;
  background: #f6f8fc;
}
#contrast-table {
  margin-top: 15px;
  width: 700px;
  height: 180px;
}
#contrast-table .cell{
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;    
}
.pic-box {
  width: 333px;
  height: 250px;
  border: 1px solid #e5eaf7;
  float: left;
  margin-left: 10px;
  margin-top: 10px;
}
.img-small {
  width: 90px;
  height: 76px;
}
img:hover {
  cursor: pointer;
}
</style>




