export default{
    //是否登录
    loggedIn: function(state){          
        if(!state.user.token){
        return false;
        }

        if(state.user.token == ''){
        return false;
        }

        return true;
    },

    //是否后端超级管理员
    isAdministrator: function(state){      
        return state.user.isBackend && state.user.roleCode == "administrator";
    },

    //是否前端管理人员
    isManager: function(state){          
        return !state.user.isBackend && state.user.roleCode == "manager";
    }
}


